/*reset*/

*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
span,
em,
small,
strong,
sub,
sup,
mark,
del,
ins,
strike,
abbr,
dfn,
blockquote,
q,
cite,
code,
pre,
ol,
ul,
li,
dl,
dt,
dd,
div,
section,
article,
main,
aside,
nav,
header,
hgroup,
footer,
img,
figure,
figcaption,
address,
time,
audio,
video,
canvas,
iframe,
details,
summary,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
	margin: 0;
	padding: 0;
	border: 0;
}

ul,
ol {
	list-style: none;
}

/*base*/

html {
	font-size: 62.5%;
}

body {
	position: relative;
	font-family: "Noto Sans JP", sans-serif;
	font-size: 1.6rem;
	font-weight: 500;
	color: #333;
	line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: #fff;
	overflow-x: hidden;
	overflow-wrap: break-word;
	min-height: 100vh;
	min-height: -webkit-fill-available;
}

a {
	color: inherit;
	outline: none;
}

img {
	max-width: 100%;
	height: auto;
	border: none;
	vertical-align: top;
}

video {
	max-width: 100%;
	height: auto;
}

.dark-text {
	color: #fff;
}

.dark-btn {
	color: #fff;
}

.header_inner {
	width: 100%;
	padding: 20px 30px 0 30px;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

.header_logo {
	font-family: "Montserrat", sans-serif;
	font-size: 2.4rem;
	font-weight: 200;
	letter-spacing: 0.05em;
}

.header_nav {
	position: absolute;
	display: none;
	top: 0;
	left: 0;
	width: 40%;
	height: 100vh;
	padding: 165px 40px 0px 100px;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: -20;
}

.nav_lists {
	display: flex;
	flex-direction: column;
}

.nav_list_link {
	display: inline-block;
	position: relative;
	font-family: "Montserrat", sans-serif;
	padding: 2rem 4rem 2rem 0;
	font-size: 8rem;
	font-weight: 100;
	text-decoration: none;
}

.nav_list_link:after {
	position: absolute;
	content: "";
	width: 4rem;
	height: 4rem;
	background-image: url(/images/icon_arrow.svg);
	background-repeat: no-repeat;
	right: 0;
	top: 50%;
	opacity: 0;
	transform: translateY(-50%);
	transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.nav_list_link:hover {
	color: #88c4dd;
	transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.nav_list_link:hover::after {
	opacity: 1;
	transform: translate(50px, -50%) scale(0.8, 0.8);
	transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.btn-menu {
	display: block;
	padding: 0 20px;
	border-radius: 20px;
	border: rgba(255, 255, 255, 0.2) 1px solid;
	background: rgba(0, 0, 0, 0.7);
}

.btn-menu_text {
	display: inline-block;
	font-size: 1.6rem;
	color: #fff;
	font-family: "Montserrat", sans-serif;
	font-weight: 200;
	line-height: 40px;
	padding: 0 1rem;
}

.btn-menu_icon svg {
	color: #fff;
	fill: currentColor;
}

.-active rect:nth-of-type(1) {
	transform: translateY(20px) rotate(-45deg);
}

.-active rect:nth-of-type(2) {
	opacity: 0;
	animation: menu-bar2 0.8s forwards;
}

.-active rect:nth-of-type(3) {
	transform: translateY(-20px) rotate(45deg);
}

.l-header {
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 20;
}

.l-btn-menu {
	position: fixed;
	right: 30px;
}

.l-section {
	width: 100%;
	max-width: 84%;
	margin: 0 auto;
}

/* title */

.section-heading {
	display: inline-block;
	font-size: 8rem;
	color: #fff;
	position: relative;
	font-family: "Montserrat", sans-serif;
	font-weight: 200;
	margin-bottom: 80px;
}

.section-heading::after {
	content: "";
	width: 160px;
	height: 2px;
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
	background-color: #fff;
}

.section-heading02 {
	display: inline-block;
	font-size: 8rem;
	color: #00558f;
	position: relative;
	font-family: "Montserrat", sans-serif;
	font-weight: 200;
	margin-bottom: 80px;
}

.section-heading02::after {
	content: "";
	width: 160px;
	height: 2px;
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
	background-color: #fff;
}

@keyframes menu-bar2 {

100% {
	height: 0;
}

}

